import Queue from './structs/queue'
import { IAlgorithmCallbacks } from '../types'
import { IGraph } from '../interface/graph';
import { INode } from '../interface/item';

/**
 * 
 * @param callbacks 
 * allowTraversal:  BFS 
 * enterNode:  BFS 
 * leaveNode:  BFS 
 */
function initCallbacks(callbacks: IAlgorithmCallbacks = {} as IAlgorithmCallbacks) {
  const initiatedCallback = callbacks;

  const stubCallback = () => { };

  const allowTraversalCallback = (
    () => {
      const seen = {};
      return ({ next }) => {
        const id = next.get('id')
        if (!seen[id]) {
          seen[id] = true;
          return true;
        }
        return false;
      };
    }
  )();

  initiatedCallback.allowTraversal = callbacks.allowTraversal || allowTraversalCallback;
  initiatedCallback.enter = callbacks.enter || stubCallback;
  initiatedCallback.leave = callbacks.leave || stubCallback;

  return initiatedCallback;
}

/**
 * 
 * @param graph Graph 
 * @param startNode 
 * @param originalCallbacks 
 */
const breadthFirstSearch = (graph: IGraph, startNodeId: string, originalCallbacks?: IAlgorithmCallbacks) => {
  const callbacks = initCallbacks(originalCallbacks)
  const nodeQueue = new Queue()

  const startNode = graph.findById(startNodeId)
  // 
  nodeQueue.enqueue(startNode)

  let previousNode = null

  // 
  while (!nodeQueue.isEmpty()) {
    const currentNode = nodeQueue.dequeue() as INode
    callbacks.enter({
      current: currentNode,
      previous: previousNode
    })

    // 
    graph.getNeighbors(currentNode, 'target').forEach((nextNode: INode) => {
      if (callbacks.allowTraversal({
        previous: previousNode,
        current: currentNode,
        next: nextNode
      })) {
        nodeQueue.enqueue(nextNode)
      }
    })

    callbacks.leave({
      current: currentNode,
      previous: previousNode
    })

    // 
    previousNode = currentNode
  }
}

export default breadthFirstSearch
